import React from 'react';
import { useState } from 'react';
import { useEffect } from 'react';
import { useParams, Link } from 'react-router-dom';
import Box from '@mui/material/Box';
import Typography from '@mui/material/Typography';
import Grid from '@mui/material/Grid';
import Paper from '@mui/material/Paper';
import Button from '@mui/material/Button'; // Import Button component from Material-UI
import Container from '@mui/material/Container'; // Import Button component from Material-UI

import CircularProgress from '@mui/material/CircularProgress';
import { blue, red } from '@mui/material/colors';

import Dialog from '@mui/material/Dialog';
import DialogActions from '@mui/material/DialogActions';
import DialogContent from '@mui/material/DialogContent';
import DialogContentText from '@mui/material/DialogContentText';
import DialogTitle from '@mui/material/DialogTitle';


// ၁. state management က အဖွဲ့တွေ
import { useSelector, useDispatch } from 'react-redux';
// react router 
import { useNavigate } from 'react-router-dom';


// ၂. မဖြစ်မနေ သုံးကြမယ့်  Slice က function တွေ
import {
  // thunks
  deleteShopAsyncThunk,
  selectMenuDeleteStatus,
  selectMenuError,
  selectSelectedMenu,
  // selectors
  selectSelectedShop,
  selectShopDeleteStatus,
  selectShopError,
  setMenuDeleteStatus,
  setSelectedItem,
  setSelectedMenu,
  // actions
  setShopDeleteStatus
// } from 'features/shop/shopSlice';
} from 'features/main/mainSlice';

import {
  // thunks
  // selectors
  selectAccessToken
  // actions
} from 'features/user/userSlice';


import {
  /*
  // selectors
  // main data (၂) မျိုး
  selectMenuList,
  selectSelectedMenu,
  // CRUD Status (၄) မျိုး
  selectMenuStatus,
  selectMenuStoreStatus,
  selectMenuUpdateStatus,
  selectMenuDeleteStatus,
  // Error (၂) မျိုး
  selectMenuError,
  selectMenuErrors,

  // actions
  // local state ပြောင်းတာတွေ
  setCategories,
  setSelectedMenu,
  updateSelectedMenu,
  // local status ပြောင်းတာတွေ
  setMenuStatus,
  setMenuStoreStatus,
  setMenuUpdateStatus,
  setMenuDeleteStatus,
  // Validation Errors တွေ set လုပ်ဖို့
  setMenuErrors,
  */

  // thunks
  // CRUD Thunks  တွေ။
  selectMenuListAsyncThunk,
  storeMenuAsyncThunk,
  updateMenuAsyncThunk,
  deleteMenuAsyncThunk,
} from 'features/menu/menuSlice';


const MenuDetailPage = () => {


  // core
  const { id } = useParams();
  const dispatch = useDispatch();
  const navigate = useNavigate();


  // internal state
  const [open, setOpen] = useState(false);


  // selector hooks
  const selectedMenu = useSelector(selectSelectedMenu);
  const accessToken = useSelector(selectAccessToken);

  // Menu section

  // shop section
  const shop = useSelector(selectSelectedShop);
  const menuDeleteStatus = useSelector(selectMenuDeleteStatus);
  const menuError = useSelector(selectMenuError);
  // const shop = shopData.find((shop) => shop.id === parseInt(id, 10));

  const handleClose = (id, access_token) => {
    console.log("id is " + id);
    console.log("access_token is " + access_token);
    if (id !== undefined) {
      // dispatch delete shop api 
      let formData = new FormData();
      formData.append("access_token", access_token);
      formData.append("id", id);
      dispatch(deleteMenuAsyncThunk(formData));
    }
    setOpen(false);
  };

  const handleErrorDialogClose = () => {
    dispatch(setMenuDeleteStatus('idle'));
  }

  const handleMenuEditClick = (Menu) => {
    // set Menu detail and
    dispatch(setSelectedMenu(Menu))
    // go to Menu detail page
    navigate(`/menus/${Menu.id}/edit`)
  }
  const handleItemCardClick = (item) => {
    // set item detail and
    dispatch(setSelectedItem(item))
    // go to item detail page
    navigate(`/items/${item.id}`)
  }
  useEffect(() => {
    if (menuDeleteStatus == "succeeded") {
      // set back to idle 
      dispatch(setMenuDeleteStatus("idle"));
      // and then go back to shops
      navigate(-1); // go back
    }
  }, [menuDeleteStatus]); // Include history as a dependency





  // Main UI Component
  

  return (

    <Container>
      <Box>
        <Typography variant="h4">{selectedMenu.name}</Typography>
        <Typography variant="body2">{selectedMenu.description}</Typography>

        <Typography variant="h5" style={{ marginTop: '20px' }}>
          Available Menus
        </Typography>
        <Button
          onClick={() => handleMenuEditClick(selectedMenu)}
          variant="contained"
          color="primary"
          style={{ marginTop: '20px', marginBottom: '20px' }}
        >


          Edit Menu Info
        </Button>

        <br></br>

        <Box sx={{ m: 1, position: 'relative' }}>

          <Button
            onClick={() => setOpen(true)}
            variant="contained"
            style={{ marginTop: '20px', marginBottom: '20px' }}
          >
            Delete Menu Info
          </Button>
          Menu Delete Status : {menuDeleteStatus}
        </Box>

        <br></br>

        <Button
          component={Link}
          to="/items/new"
          variant="contained"
          color="primary"
          style={{ marginTop: '20px', marginBottom: '20px' }}
        >
          Create New Item
        </Button>
        <Grid container spacing={3}>
          {selectedMenu.items.map((item) => (
            <Grid item xs={12} sm={6} md={4} key={item.id}>
              <Container onClick={() => handleItemCardClick(item)} >
                <Paper elevation={3} style={{ padding: '16px', cursor: 'pointer' }}>
                  <Grid container spacing={3}>
                    <Grid item xs={4} key={item.id}>
                      <img src={item.background_image_url} style={{maxWidth:'100%'}}/>
                    </Grid>
                    <Grid item xs={8} key={item.id}>
                      <Typography variant="h6">{item.name}</Typography>
                      <Typography variant="body2">{item.description}</Typography>
                      { /* photo ပြဖို့ လိုမယ် */}
                    </Grid>
                  </Grid>
                </Paper>
              </Container>
            </Grid>
          ))}
        </Grid>


      </Box>



      {/* delete confirmation dialog */}
      <Dialog
        open={open}
        // onClose={() => handleClose(id)}
        aria-labelledby="alert-dialog-title"
        aria-describedby="alert-dialog-description"
      >
        <DialogTitle id="alert-dialog-title">
          Delete Confirmation!
        </DialogTitle>
        <DialogContent>
          <DialogContentText id="alert-dialog-description">
            Are you sure to delete this Menu info?
          </DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button onClick={() => handleClose(0)} autoFocus>
            Cancel
          </Button>
          <Button onClick={() => handleClose(id, accessToken)} autoFocus>
            OK
          </Button>
        </DialogActions>
      </Dialog>


      {/* delete error dialog */}
      <Dialog
        open={menuDeleteStatus == 'error'}
        aria-labelledby="alert-dialog-title"
        aria-describedby="alert-dialog-description"
      >
        <DialogTitle id="alert-dialog-title">
          Error!
        </DialogTitle>
        <DialogContent>
          <DialogContentText id="alert-dialog-description">
            {menuError}
          </DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button onClick={handleErrorDialogClose} autoFocus>
            OK
          </Button>
        </DialogActions>
      </Dialog>
    </Container>
  );
};

export default MenuDetailPage;
