import React from 'react';
import { useState } from 'react';
import { useEffect } from 'react';
import { useParams, Link } from 'react-router-dom';
import Box from '@mui/material/Box';
import Typography from '@mui/material/Typography';
import Grid from '@mui/material/Grid';
import Paper from '@mui/material/Paper';
import Button from '@mui/material/Button'; // Import Button component from Material-UI
import Container from '@mui/material/Container'; // Import Button component from Material-UI

import CircularProgress from '@mui/material/CircularProgress';
import { blue, red } from '@mui/material/colors';

import Dialog from '@mui/material/Dialog';
import DialogActions from '@mui/material/DialogActions';
import DialogContent from '@mui/material/DialogContent';
import DialogContentText from '@mui/material/DialogContentText';
import DialogTitle from '@mui/material/DialogTitle';


// ၁. state management က အဖွဲ့တွေ
import { useSelector, useDispatch } from 'react-redux';
// react router 
import { useNavigate } from 'react-router-dom';
import { selectCategoryDeleteStatus, selectCategoryErrors, selectCategoryStoreStatus, selectSelectedCategory, setCategoryDeleteStatus, setSelectedCategory, setSelectedMenu } from 'features/main/mainSlice';
import { selectAccessToken } from 'features/user/userSlice';

import { deleteCategoryAsyncThunk } from 'features/category/categorySlice';



/*​ ဒီမှာ ဘာတွေ လိုမလဲ? */
/*
  Category Detail

  Data အနေနနဲ့
  - selectedCategory
  - access token
  - category delete status 
  
  Action အနေနဲ့
  - delete category 

*/
// ၂. မဖြစ်မနေ သုံးကြမယ့်  Slice က function တွေ


const CategoryDetailPage = () => {


  // core
  const { id } = useParams();
  const dispatch = useDispatch();
  const navigate = useNavigate();


  // internal state
  const [open, setOpen] = useState(false);


  // selector hooks
  const selectedCategory = useSelector(selectSelectedCategory);
  const accessToken = useSelector(selectAccessToken);
  const categoryDeleteStatus = useSelector(selectCategoryDeleteStatus);
  const categoryError = useSelector(selectCategoryStoreStatus);
  const categoryErrors = useSelector(selectCategoryErrors);

  const handleClose = (id, access_token) => {
    console.log("id is " + id);
    console.log("access_token is " + access_token);
    if (id !== undefined) {
      // dispatch delete shop api 
      let formData = new FormData();
      formData.append("access_token", access_token);
      formData.append("id", id);
      dispatch(deleteCategoryAsyncThunk(formData));
    }
    setOpen(false);
  };

  const handleErrorDialogClose = () => {
    dispatch(setCategoryDeleteStatus('idle'));
  }


  const handleCategoryEditClick = (category) => {
    // set category detail and
    dispatch(setSelectedCategory(category))
    // go to category detail page
    navigate(`/categories/${category.id}/edit`)
  }
  const handleMenuCardClick = (menu) => {
    // set menu detail and
    dispatch(setSelectedMenu(menu));
    // go to menu detail page
    navigate(`/menus/${menu.id}`)
  }
  useEffect(() => {
    if (categoryDeleteStatus == "succeeded") {
      // set back to idle 
      dispatch(setCategoryDeleteStatus("idle"));
      // and then go back to shops
      navigate(-1); // go back
    }
  }, [categoryDeleteStatus]); // Include history as a dependency





  // Main UI Component
  return (
    <Container>
      <Box>
        <Typography variant="h4">{selectedCategory.name}</Typography>
        <Typography variant="body2">{selectedCategory.description}</Typography>

        <Typography variant="h5" style={{ marginTop: '20px' }}>
          Available Menus
        </Typography>
        <Button
          onClick={() => handleCategoryEditClick(selectedCategory)}
          variant="contained"
          color="primary"
          style={{ marginTop: '20px', marginBottom: '20px' }}
        >
          Edit Category Info
        </Button>

        <br></br>

        <Box sx={{ m: 1, position: 'relative' }}>

          <Button
            onClick={() => setOpen(true)}
            variant="contained"
            style={{ marginTop: '20px', marginBottom: '20px' }}
          >
            Delete Categroy Info
          </Button>
          Category Delete Status : {categoryDeleteStatus}
        </Box>

        <br></br>

        <Button
          component={Link}
          to="/menus/new"
          variant="contained"
          color="primary"
          style={{ marginTop: '20px', marginBottom: '20px' }}
        >
          Create New Menu
        </Button>
        <Grid container spacing={3}>
          {selectedCategory.menus.map((menu,index) => (
            <Grid item xs={12} sm={6} md={4} key={index}>
              <Container onClick={() => handleMenuCardClick(menu)} >
                <Paper elevation={3} style={{ padding: '16px', cursor: 'pointer' }}>
                  <Grid container spacing={3}>
                    <Grid item xs={4} key={menu.id}>
                      <img src={menu.background_image_url} style={{maxWidth:'100%'}}/>
                    </Grid>
                    <Grid item xs={8} key={menu.id}>
                      <Typography variant="h6">{menu.name}</Typography>
                      <Typography variant="body2">{menu.description}</Typography>
                      { /* photo ပြဖို့ လိုမယ် */}
                    </Grid>
                  </Grid>
                </Paper>
              </Container>
            </Grid>
          ))}
        </Grid>


      </Box>



      {/* delete confirmation dialog */}
      <Dialog
        open={open}
        // onClose={() => handleClose(id)}
        aria-labelledby="alert-dialog-title"
        aria-describedby="alert-dialog-description"
      >
        <DialogTitle id="alert-dialog-title">
          Delete Confirmation!
        </DialogTitle>
        <DialogContent>
          <DialogContentText id="alert-dialog-description">
            Are you sure to delete this category info?
          </DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button onClick={() => handleClose(0)} autoFocus>
            Cancel
          </Button>
          <Button onClick={() => handleClose(id, accessToken)} autoFocus>
            OK
          </Button>
        </DialogActions>
      </Dialog>


      {/* delete error dialog */}
      <Dialog
        open={categoryDeleteStatus == 'error'}
        aria-labelledby="alert-dialog-title"
        aria-describedby="alert-dialog-description"
      >
        <DialogTitle id="alert-dialog-title">
          Error!
        </DialogTitle>
        <DialogContent>
          <DialogContentText id="alert-dialog-description">
            {categoryError}
          </DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button onClick={handleErrorDialogClose} autoFocus>
            OK
          </Button>
        </DialogActions>
      </Dialog>
    </Container>
  );
};

export default CategoryDetailPage;
