import MenuNewFormPage from 'presentation/pages/menu/MenuNewFormPage';
import MenuDetailPage from 'presentation/pages/menu/MenuDetailPage';
import MenuEditFormPage from 'presentation/pages/menu/MenuEditFormPage';
import ItemNewFormPage from 'presentation/pages/item/ItemNewFormPage';
import ItemDetailPage from 'presentation/pages/item/ItemDetailPage';
import ItemEditFormPage from 'presentation/pages/item/ItemEditFormPage';

//import CategoryNewFormPage from './presentation/pages/category/CategoryNewFormPage';

// import CategoryNewFormPage from './presentation/pages/category/CategoryNewFormPage';

const container = document.getElementById('root');
const root = createRoot(container);

root.render(
  // <React.StrictMode>
    <Provider store={store}>
      <BrowserRouter>
        <Routes>
          <Route path="/" element={<Layout />} />
          <Route index element={<LandingPage />} />
          <Route path="/app" element={<App />} />
          <Route path="/drawer" element={<DrawerPage />} />
          <Route path="/login" element={<LoginPage />} />
          <Route path="/logout" element={<LogoutPage />} />
          <Route path="/register" element={<RegisterPage />} />
          <Route path="/forgot-password" element={<ForgotPasswordPage />} />
          <Route path="/confirm-otp" element={<ConfirmOtpPage />} />
          <Route path="/dashboard" element={<UserDashboardPage />} />
          <Route path="/shops" element={<ShopListPage />} />
          <Route path="/shops/new" element={<ShopNewFormPage />} />
          <Route path="/shops/:id" element={<ShopDetailPage />} />
          <Route path="/shops/:id/edit" element={<ShopEditFormPage />} />

          <Route path="/categories/new" element={<CategoryNewFormPage />} />
          <Route path="/categories/:id" element={<CategoryDetailPage />} />
          <Route path="/categories/:id/edit" element={<CategoryEditFormPage />} />



          <Route path="/menus/new" element={<MenuNewFormPage />} />
          <Route path="/menus/:id" element={<MenuDetailPage />} />
          <Route path="/menus/:id/edit" element={<MenuEditFormPage />} />


          <Route path="/items/new" element={<ItemNewFormPage />} />
          <Route path="/items/:id" element={<ItemDetailPage />} />
          <Route path="/items/:id/edit" element={<ItemEditFormPage />} />
        </Routes>
      </BrowserRouter>
    </Provider>
  // </React.StrictMode>
);

