import Button from "@mui/material/Button";
import TextField from "@mui/material/TextField";
import FormControlLabel from "@mui/material/FormControlLabel";
import Checkbox from "@mui/material/Checkbox";
import Link from "@mui/material/Link";
import Grid from "@mui/material/Grid";
import Box from "@mui/material/Box";
import Typography from "@mui/material/Typography";
import Container from "@mui/material/Container";
import CircularProgress from '@mui/material/CircularProgress';
import { blue, red } from '@mui/material/colors';
import { DropzoneArea } from 'material-ui-dropzone';
// core import
import { useSelector, useDispatch } from 'react-redux';
import { useNavigate } from 'react-router-dom';
import React, { useEffect, useState } from 'react';

// Dialogs

import Dialog from '@mui/material/Dialog';
import DialogActions from '@mui/material/DialogActions';
import DialogContent from '@mui/material/DialogContent';
import DialogContentText from '@mui/material/DialogContentText';
import DialogTitle from '@mui/material/DialogTitle';

import {
  // selectors
  loginStatus,
  loginError,
  selectUserRegistrationValidationError,
  selectAccessToken,

  // actions

  // thunks
} from './../../../features/user/userSlice';




import {
  // selectors
  selectShopStoreStatus,
  selectShopError,
  selectShopErrors,
  // actions
  setErrors,
  setShopStoreStatus,
  // thunks
  storeShopAsyncThunk,
} from './../../../features/shop/shopSlice';



const ShopNewFormPage = () => {

  // core
  const navigate = useNavigate();
  const dispatch = useDispatch();

  // internal state
  const [formData, setFormData] = useState({
    name: '',
    email: '',
    password: '',
    confirm_password: '',
  });
  const [files, setFiles] = useState({
    background_image: null,
    shop_image: null
  });
  // const [errors, setErrors] = useState({}); // Object to store validation errors



  // selector hooks
  // shop
  const status = useSelector(selectShopStoreStatus);
  const error = useSelector(selectShopError);
  const errors = useSelector(selectShopErrors);
  // user
  const access_token = useSelector(selectAccessToken);

  const handleChange = (e) => {
    const { name, value } = e.target;
    console.log({ name, value });
    setFormData({ ...formData, [name]: value });
  };


  const handleSubmit = (event) => {
    console.log(event);
    event.preventDefault();

    // validate form data
    // Validation logic
    const validationErrors = {};
    if (!formData.name) {
      validationErrors.name = 'Name is required';
    }

    if (!formData.description) {
      validationErrors.description = 'Description is required';
    }

    if (!formData.address) {
      validationErrors.address = 'Address is required';
    }

    if (!formData.contact) {
      validationErrors.contact = 'Contact is required';
    }

    if (!formData.url) {
      validationErrors.url = 'URL is required';
    }

    // alert(files["background_image"]);

    if (files["background_image"] === null) {
      validationErrors.background_image = 'Background Image is required';
    }
    if (files["shop_image"] === null) {
      validationErrors.shop_image = 'Shop Image is required';
    }


    console.log(validationErrors);

    if (Object.keys(validationErrors).length > 0) {
      // alert("validation error");
      // setErrors(validationErrors);
      dispatch(setErrors(validationErrors));
    } else {
      // Submit the form or send data to the server
      // setErrors({}); // Clear any previous errors
      dispatch(setErrors({}));
      // console.log(files["background_image"]);

      const data = new FormData(event.target);

      if (files["background_image"] !== undefined) {
        //alert("background image exist");
        data.append('background_image', files["background_image"]);
      }
      if (files["shop_image"] !== undefined) {
        //alert("shop_image exist");
        data.append('shop_image', files["shop_image"]);
      }
      //formData.append('background_image', files["background_image"].length == 0 ? null :  files["background_image"][0]);
      //formData.append('shop_image', files["shop_image"].length == 0 ? null :  files["shop_image"][0]);
      // formData.append('shop_image', files["shop_image"][0]);
      console.log(data.get("background_image")); // You can send this data to your server for registration
      console.log(data.get("shop_image"));
      console.log(data.get("access_token"));
      // dispatch(registerThunk(formData));
      // return;
      // this is form data
      dispatch(storeShopAsyncThunk(data));
    }

    // return;




    /*
    const data = new FormData(event.currentTarget);
    let email = data.get("email");
    let password = data.get("password");
    let confirm_password = data.get("confirm_password");
    let name = data.get("name");
    let postData = { name, email, password, confirm_password };
    console.log(postData);
    // TODO: client side validation
    // dispatch register
    */
    // dispatch(registerThunk(postData));
  };

  const handleFileChanges = (event) => {
    const { name, current_files } = event;
    console.log('Name & Files:', name, current_files);
    setFiles({ ...files, [name]: current_files.length == 0 ? null : current_files[0] });

  }


  const handleClose = () => {
    dispatch(setShopStoreStatus('idle'));
  };


  useEffect(() => {
    if(status == "succeeded"){
      // need to dispatch to set status as idle
      dispatch(setShopStoreStatus("idle"));
      navigate("/shops");
    }
  }, [status]); // Include history as a dependency


  return (
    <Container component="main" maxWidth="xs">
      <Box
        sx={{
          marginTop: 8,
          display: "flex",
          flexDirection: "column",
          alignItems: "center",
        }}
      >
        <Typography component="h1" variant="h5">
          Register
        </Typography>
        <Box component="form" onSubmit={handleSubmit} noValidate sx={{ mt: 1 }}>
          <input type="hidden" value={access_token} name="access_token" placeholder="Hidden Input" />
          <TextField
            margin="normal"
            required
            fullWidth
            id="name"
            label="Name"
            name="name"
            autoComplete="name"
            autoFocus
            onChange={handleChange}
            error={errors.hasOwnProperty("name")}
            helperText={errors.name}
          />
          <TextField
            margin="normal"
            required
            fullWidth
            id="description"
            label="Description"
            name="description"
            autoComplete="description"
            onChange={handleChange}
            error={errors.hasOwnProperty("description")}
            helperText={errors.description}
          />
          <TextField
            margin="normal"
            required
            fullWidth
            id="address"
            label="Address"
            name="address"
            autoComplete="address"
            onChange={handleChange}
            error={errors.hasOwnProperty("address")}
            helperText={errors.address}
          />
          <TextField
            margin="normal"
            required
            fullWidth
            id="contact"
            label="Contact"
            name="contact"
            autoComplete="contact"
            onChange={handleChange}
            error={errors.hasOwnProperty("contact")}
            helperText={errors.contact}
          />
          <TextField
            margin="normal"
            required
            fullWidth
            id="url"
            label="URL"
            name="url"
            autoComplete="url"
            onChange={handleChange}
            error={errors.hasOwnProperty("url")}
            helperText={errors.url}
          />
          <TextField
            margin="normal"
            fullWidth
            id="wifi_name"
            label="Wifi Name"
            name="wifi_name"
            autoComplete="wifi_name"
            onChange={handleChange}
            error={errors.hasOwnProperty("wifi_name")}
            helperText={errors.wifi_name}
          />
          <TextField
            margin="normal"
            fullWidth
            id="wifi_password"
            label="Wifi Password"
            name="wifi_password"
            autoComplete="wifi_password"
            onChange={handleChange}
            error={errors.hasOwnProperty("wifi_password")}
            helperText={errors.wifi_password}
          />

          <Typography>Background Image {files["background_image"]?.name}</Typography>
          <DropzoneArea
            acceptedFiles={['image/*']}
            onChange={(selectedFiles) => handleFileChanges({ "current_files": selectedFiles, "name": "background_image" })}
          />
          <Typography style={{ color: '#d32f2f', fontSize: '0.75rem' }} >{errors.hasOwnProperty("background_image") ? errors.background_image : ""}</Typography>


          <Typography sx={{ marginTop: 2 }}>Shop Image {files["shop_image"]?.name} </Typography>
          <DropzoneArea
            acceptedFiles={['image/*']}
            onChange={(selectedFiles) => handleFileChanges({ "current_files": selectedFiles, "name": "shop_image" })}
          />
          <Typography style={{ color: '#d32f2f', fontSize: '0.75rem' }} >{errors.hasOwnProperty("shop_image") ? errors.shop_image : ""}</Typography>



          <Box sx={{ m: 1, position: 'relative' }}>
            <Button
              type="submit"
              fullWidth
              variant="contained"
              sx={{ mt: 3, mb: 2 }}
              disabled={status == 'loading'}
            >
              Register
            </Button>
            {status == 'loading' && (
              <CircularProgress
                size={24}
                sx={{
                  color: blue[500],
                  position: 'absolute',
                  top: '50%',
                  left: '50%',
                  marginTop: '-12px',
                  marginLeft: '-12px',
                }}
              />
            )}
          </Box>


          <Grid container>
            <Grid item xs>

            </Grid>
            <Grid item>
              <Link href="/shops" variant="body2">
                {"Back to shop list"}
              </Link>
            </Grid>
          </Grid>
        </Box>
      </Box>

      <Dialog
        open={status == "error"}
        onClose={handleClose}
        aria-labelledby="alert-dialog-title"
        aria-describedby="alert-dialog-description"
      >
        <DialogTitle id="alert-dialog-title">
          {"Error"}
        </DialogTitle>
        <DialogContent>
          <DialogContentText id="alert-dialog-description">
            {error}
          </DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button onClick={handleClose} autoFocus>
            OK
          </Button>
        </DialogActions>
      </Dialog>
    </Container>
  );
};
export default ShopNewFormPage;