import React from 'react';
import { useState } from 'react';
import { useEffect } from 'react';
import { useParams, Link } from 'react-router-dom';
import Box from '@mui/material/Box';
import Typography from '@mui/material/Typography';
import Grid from '@mui/material/Grid';
import Paper from '@mui/material/Paper';
import Button from '@mui/material/Button'; // Import Button component from Material-UI
import Container from '@mui/material/Container'; // Import Button component from Material-UI

import CircularProgress from '@mui/material/CircularProgress';
import { blue, red } from '@mui/material/colors';

import Dialog from '@mui/material/Dialog';
import DialogActions from '@mui/material/DialogActions';
import DialogContent from '@mui/material/DialogContent';
import DialogContentText from '@mui/material/DialogContentText';
import DialogTitle from '@mui/material/DialogTitle';


// ၁. state management က အဖွဲ့တွေ
import { useSelector, useDispatch } from 'react-redux';
// react router 
import { useNavigate } from 'react-router-dom';


// ၂. မဖြစ်မနေ သုံးကြမယ့်  Slice က function တွေ
import {
  // thunks
  deleteShopAsyncThunk,
  // selectors
  selectSelectedShop,
  selectShopDeleteStatus,
  selectShopError,
  // actions
  setShopDeleteStatus
} from 'features/main/mainSlice';

import {
  // thunks
  // selectors
  selectAccessToken
  // actions
} from 'features/user/userSlice';

import {
    // selectors
    selectSelectedMenu,
  } from 'features/main/mainSlice';

import {
  // selectors
  // main data (၂) မျိုး
  selectItemList,
  selectSelectedItem,
  // CRUD Status (၄) မျိုး
  selectItemStatus,
  selectItemStoreStatus,
  selectItemUpdateStatus,
  selectItemDeleteStatus,
  // Error (၂) မျိုး
  selectItemError,
  selectItemErrors,

  // actions
  // local state ပြောင်းတာတွေ
  setCategories,
  setSelectedItem,
  updateSelectedItem,
  // local status ပြောင်းတာတွေ
  setItemStatus,
  setItemStoreStatus,
  setItemUpdateStatus,
  setItemDeleteStatus,
  // Validation Errors တွေ set လုပ်ဖို့
  setItemErrors,
  
} from 'features/main/mainSlice';

import {
  // thunks
  // CRUD Thunks  တွေ။
  selectItemListAsyncThunk,
  storeItemAsyncThunk,
  updateItemAsyncThunk,
  deleteItemAsyncThunk,
} from 'features/item/itemSlice';

/*
  Item Detail မှာ ဘာတွေပါလဲ?
  Item ကိုပြမယ်။

  Edit နဲ့ Delete ခလုပ်ပါမယ်။


  @input
  - selected item , yeah that's all
  - access token

  @operations
  - delete item

  @link
  - edit page

*/
const ItemDetailPage = () => {
  // core
  const { id } = useParams();
  const dispatch = useDispatch();
  const navigate = useNavigate();

  // internal state
  const [open, setOpen] = useState(false);


  // selector hooks
  const selectedItem = useSelector(selectSelectedItem);
  const accessToken = useSelector(selectAccessToken);

  // Item section

  // shop section
  const shop = useSelector(selectSelectedShop);
  const selectedMenu = useSelector(selectSelectedMenu);
  const ItemDeleteStatus = useSelector(selectItemDeleteStatus);
  const ItemError = useSelector(selectItemError);
  // const shop = shopData.find((shop) => shop.id === parseInt(id, 10));

  const handleClose = (id, access_token) => {
    console.log("id is " + id);
    console.log("access_token is " + access_token);
    if (id !== undefined) {
      // dispatch delete shop api 
      let formData = new FormData();
      formData.append("access_token", access_token);
      formData.append("id", id);
      dispatch(deleteItemAsyncThunk(formData));
    }
    setOpen(false);
  };

  const handleErrorDialogClose = () => {
    dispatch(setItemDeleteStatus('idle'));
  }

  const handleItemEditClick = (item) => {
    // set Item detail and
    dispatch(setSelectedItem(item))
    // go to Item detail page
    navigate(`/items/${item.id}/edit`)
  }
  
  useEffect(() => {
    if (ItemDeleteStatus == "succeeded") {
      // set back to idle 
      dispatch(setItemDeleteStatus("idle"));
      // and then go back to shops
      navigate(-1); // go back
    }
  }, [ItemDeleteStatus]); // Include history as a dependency


  return (

    <Container>
      <Box>
        <Typography variant="h4">{selectedItem.name}</Typography>
        <Typography variant="body2">{selectedItem.description}</Typography>
         
        <img src={selectedItem.background_image_url} style={{maxWidth:'100%'}}/>
         
        <Button
          onClick={() => handleItemEditClick(selectedItem)}
          variant="contained"
          color="primary"
          style={{ marginTop: '20px', marginBottom: '20px' }}
        >


          Edit Item Info
        </Button>

        <br></br>

        <Box sx={{ m: 1, position: 'relative' }}>

          <Button
            onClick={() => setOpen(true)}
            variant="contained"
            style={{ marginTop: '20px', marginBottom: '20px' }}
          >
            Delete Item Info
          </Button>
          Item Delete Status : {ItemDeleteStatus}
        </Box>

        
        


      </Box>



      {/* delete confirmation dialog */}
      <Dialog
        open={open}
        // onClose={() => handleClose(id)}
        aria-labelledby="alert-dialog-title"
        aria-describedby="alert-dialog-description"
      >
        <DialogTitle id="alert-dialog-title">
          Delete Confirmation!
        </DialogTitle>
        <DialogContent>
          <DialogContentText id="alert-dialog-description">
            Are you sure to delete this Item info?
          </DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button onClick={() => handleClose(0)} autoFocus>
            Cancel
          </Button>
          <Button onClick={() => handleClose(id, accessToken)} autoFocus>
            OK
          </Button>
        </DialogActions>
      </Dialog>


      {/* delete error dialog */}
      <Dialog
        open={ItemDeleteStatus == 'error'}
        aria-labelledby="alert-dialog-title"
        aria-describedby="alert-dialog-description"
      >
        <DialogTitle id="alert-dialog-title">
          Error!
        </DialogTitle>
        <DialogContent>
          <DialogContentText id="alert-dialog-description">
            {ItemError}
          </DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button onClick={handleErrorDialogClose} autoFocus>
            OK
          </Button>
        </DialogActions>
      </Dialog>
    </Container>
  );
};

export default ItemDetailPage;
