import Button from "@mui/material/Button";
import TextField from "@mui/material/TextField";
import FormControlLabel from "@mui/material/FormControlLabel";
import Checkbox from "@mui/material/Checkbox";
import Link from "@mui/material/Link";
import Grid from "@mui/material/Grid";
import Box from "@mui/material/Box";
import Typography from "@mui/material/Typography";
import Container from "@mui/material/Container";
import CircularProgress from '@mui/material/CircularProgress';
import { blue, red } from '@mui/material/colors';
import { DropzoneArea } from 'material-ui-dropzone';
// core import
import { useSelector, useDispatch } from 'react-redux';
import { useNavigate } from 'react-router-dom';
import React, { useEffect, useState } from 'react';

// Dialogs

import Dialog from '@mui/material/Dialog';
import DialogActions from '@mui/material/DialogActions';
import DialogContent from '@mui/material/DialogContent';
import DialogContentText from '@mui/material/DialogContentText';
import DialogTitle from '@mui/material/DialogTitle';

import {
    // selectors
    loginStatus,
    loginError,
    selectUserRegistrationValidationError,
    selectAccessToken,

    // actions

    // thunks
    // } from './../../../features/user/userSlice';
} from 'features/user/userSlice';



import {
    // selectors
    // main data (၂) မျိုး
    selectItemList,
    selectSelectedItem,
    // CRUD Status (၄) မျိုး
    selectItemStatus,
    selectItemStoreStatus,
    selectItemUpdateStatus,
    // selectItemUpdateStatus,
    selectItemDeleteStatus,
    // Error (၂) မျိုး
    selectItemError,
    selectItemErrors,

    // actions
    // local state ပြောင်းတာတွေ
    setCategories,
    setSelectedItem,
    updateSelectedItem,
    // local status ပြောင်းတာတွေ
    setItemStatus,
    setItemStoreStatus,
    setItemUpdateStatus,
    setItemDeleteStatus,
    // Validation Errors တွေ set လုပ်ဖို့
    setItemErrors,
    
} from 'features/main/mainSlice';

import {
// thunks
// CRUD Thunks  တွေ။
selectItemListAsyncThunk,
storeItemAsyncThunk,
updateItemAsyncThunk,
deleteItemAsyncThunk,
} from 'features/item/itemSlice';



/*
    @input
    selected item
    access token

    @operation
    update item info

    @links
    back

*/

const ItemEditFormPage = () => {

    // core
    const navigate = useNavigate();
    const dispatch = useDispatch();

    // internal state
    const [formData, setFormData] = useState({
        placement: 0,
        shop_id: 0,
        name: '',
        description: '',
    });


    const [files, setFiles] = useState({
        background_image: null
    });



    // selector hooks
    // item
    // လိုတာတွေပဲ Select လုပ်မလား?
    // အကုန် ယူမလား?
    // module တစ်ခုမှာ လိုတာတွေပဲယူလိုက်တာ ကောင်းပါတယ်။
    const itemUpdateStatus = useSelector(selectItemUpdateStatus);
    const error = useSelector(selectItemError);
    const errors = useSelector(selectItemErrors);
    // ဒါက supllimentry လိုတဲ့ data တွေ။
    // shop
    const selectedItem = useSelector(selectSelectedItem);
    // user
    // ဒီမှာလည်း 
    const accessToken = useSelector(selectAccessToken);

    const handleChange = (e) => {
        const { name, value } = e.target;
        console.log({ name, value });
        dispatch(updateSelectedItem({ name, value }));
        setFormData({ ...formData, [name]: value });
    };

    const handleSubmit = (event) => {
        console.log(event);
        event.preventDefault();

        // validate form data
        // Validation logic
        const validationErrors = {};
        // skip client side validation

        /*
        if (!formData.name) {
          validationErrors.name = 'Name is required';
        }
    
        if (!formData.description) {
          validationErrors.description = 'Description is required';
        }
        */
        console.log(validationErrors);

        if (Object.keys(validationErrors).length > 0) {
            // alert("validation error");
            // setErrors(validationErrors);
            dispatch(setItemErrors(validationErrors));
        } else {
            dispatch(setItemErrors({}));
            const data = new FormData(event.target);
            // add files 
            if (files["background_image"] !== undefined) {
                data.append('background_image', files["background_image"]);
            }
            dispatch(updateItemAsyncThunk(data));
        }
    };




    const handleClose = () => {
        dispatch(setItemUpdateStatus('idle'));
    };


    const handleFileChanges = (event) => {
        const { name, current_files } = event;
        console.log('Name & Files:', name, current_files);
        setFiles({ ...files, [name]: current_files.length == 0 ? null : current_files[0] });

    }

    useEffect(() => {
        if (itemUpdateStatus == "succeeded") {
            dispatch(setItemUpdateStatus("idle"));
            navigate(-1); // This is equivalent to pressing the back button
        }
    }, [itemUpdateStatus]);


    return (
        <Container component="main" maxWidth="xs">
            <Box
                sx={{
                    marginTop: 8,
                    display: "flex",
                    flexDirection: "column",
                    alignItems: "center",
                }}
            >
                <Typography component="h1" variant="h5">
                    Edit Item
                </Typography>
                <Box component="form" onSubmit={handleSubmit} noValidate sx={{ mt: 1 }}>
                    <input type="hidden" name="id" value={selectedItem.id} placeholder="Hidden Input for Selected Item Id" />

                    <input type="hidden" value={accessToken} name="access_token" placeholder="Hidden Input" />
                    <input type="hidden" value={selectedItem.id} name="id" placeholder="Hidden Input for Selected Item Id" />
                    <input type="hidden" value={selectedItem.menu_id} name="menu_id" placeholder="Hidden Input for Selected Menu Id" />
                    <input type="hidden" value={0} name="placement" placeholder="Item Placement" />
                    <input type="hidden" value={1} name="is_active" placeholder="Active Flag" />
                    <input type="hidden" value={0} name="visit_count" placeholder="Visit Count" />
                    { /* name */}
                    <TextField
                        name="name"
                        value={selectedItem.name ?? ""}
                        id="name"
                        label="Name"
                        onChange={handleChange}
                        error={errors.hasOwnProperty("name")}
                        helperText={errors.name}

                        autoComplete="name"
                        margin="normal"
                        required
                        fullWidth
                        autoFocus
                    />

                    { /* description */}
                    <TextField
                        name="description"
                        value={selectedItem.description ?? ""}
                        id="description"
                        label="Description"
                        autoComplete="description"
                        onChange={handleChange}
                        error={errors.hasOwnProperty("description")}
                        helperText={errors.description}

                        margin="normal"
                        required
                        fullWidth
                    />

                    { /* old price */}
                    <TextField
                        name="old_price"
                        id="old_price"
                        value={selectedItem.old_price ?? ""}
                        label="Old Price"
                        autoComplete="old_price"
                        onChange={handleChange}
                        error={errors.hasOwnProperty("old_price")}
                        helperText={errors.old_price}

                        margin="normal"
                        required
                        fullWidth
                    />

                    { /* current price */}
                    <TextField
                        name="current_price"
                        value={selectedItem.current_price ?? ""}
                        id="current_price"
                        label="Current Price"
                        autoComplete="current_price"
                        onChange={handleChange}
                        error={errors.hasOwnProperty("current_price")}
                        helperText={errors.old_price}

                        margin="normal"
                        required
                        fullWidth
                    />

                    { /* quantity */}
                    <TextField
                        name="quantity"
                        value={selectedItem.quantity ?? ""}
                        id="quantity"
                        label="Quantity"
                        autoComplete="quantity"
                        onChange={handleChange}
                        error={errors.hasOwnProperty("quantity")}
                        helperText={errors.quantity}

                        margin="normal"
                        required
                        fullWidth
                    />

                    { /* background image */}
                    <Typography>Background Image {files["background_image"]?.name}</Typography>
                    <DropzoneArea
                        acceptedFiles={['image/*']}
                        onChange={(selectedFiles) => handleFileChanges({ "current_files": selectedFiles, "name": "background_image" })}
                    />
                    <Typography style={{ color: '#d32f2f', fontSize: '0.75rem' }} >{errors.hasOwnProperty("background_image") ? errors.background_image : ""}</Typography>


                    <Box sx={{ m: 1, position: 'relative' }}>
                        <Button
                            type="submit"
                            fullWidth
                            variant="contained"
                            sx={{ mt: 3, mb: 2 }}
                            disabled={itemUpdateStatus == 'loading'}
                        >
                            Update Item
                        </Button>
                        {itemUpdateStatus == 'loading' && (
                            <CircularProgress
                                size={24}
                                sx={{
                                    color: blue[500],
                                    position: 'absolute',
                                    top: '50%',
                                    left: '50%',
                                    marginTop: '-12px',
                                    marginLeft: '-12px',
                                }}
                            />
                        )}
                    </Box>


                    <Grid container>
                        <Grid item xs>

                        </Grid>
                        <Grid item>
                            <Link href="/shops" variant="body2">
                                {"Back to shop list"}
                            </Link>
                        </Grid>
                    </Grid>
                </Box>
            </Box>

            <Dialog
                open={itemUpdateStatus == "error"}
                onClose={handleClose}
                aria-labelledby="alert-dialog-title"
                aria-describedby="alert-dialog-description"
            >
                <DialogTitle id="alert-dialog-title">
                    {"Error"}
                </DialogTitle>
                <DialogContent>
                    <DialogContentText id="alert-dialog-description">
                        {error}
                    </DialogContentText>
                </DialogContent>
                <DialogActions>
                    <Button onClick={handleClose} autoFocus>
                        OK
                    </Button>
                </DialogActions>
            </Dialog>
        </Container>
    );
};
export default ItemEditFormPage;