import axios from 'axios';

import { CATEGORY_API_ENDPOINT } from '../../config/config';
import { WAITING_TIME } from '../../config/config';

// GET REQUEST
export function selectCategoryListApi({ access_token, page }) {
    return new Promise((resolve, reject) => {
        const config = {
            headers: {
                'Authorization': `Bearer ${access_token}`
            }
        };
        const ENDPOINT = CATEGORY_API_ENDPOINT + "?page=" + page;
        axios.get(ENDPOINT, config)
            .then(response => {
                setTimeout(() => resolve(response.data), 3000);
                //resolve(response.data);
            })
            .catch(error => {
                setTimeout(() => reject(error), 3000);
                // reject(error);
            });
    });
}
export function storeCategoryApi(formData) {
    console.log("storeCategoryApi");
    console.log(formData);
    const access_token = formData.get("access_token");
    const config = {
        headers: {
            'Authorization': `Bearer ${access_token}`
        }
    };
    return new Promise((resolve, reject) => {
        axios.post(CATEGORY_API_ENDPOINT, formData, config)
            .then(response => {
                setTimeout(() => resolve(response.data), WAITING_TIME);
                //resolve(response.data);
            })
            .catch(error => {
                console.error("categoryAPI->storeCategoryApi failed:", error);
                if (error.response) {
                    // The request was made, but the server responded with a status code other than 2xx
                    if (error.response.status === 404) {
                        reject(error.response.data.message);
                    }
                    reject({ "message": JSON.stringify(error.response.data.errors) });
                } else if (error.request) {
                    // The request was made but no response was received
                    console.error("No response received. The request may have failed.");
                    reject("No response received");
                } else {
                    // Something else went wrong
                    console.error("An error occurred while making the request:", error.message);
                    reject("Request failed: " + error.message);
                }
            });
    });
}
export function updateCategoryApi(formData) {
    // getting data
    const access_token = formData.get("access_token");
    const id = formData.get("id");
    // prepare config
    // header
    const config = {
        headers: {
            'Authorization': `Bearer ${access_token}`
        }
    };
    // endpoint
    const ENDPOINT = CATEGORY_API_ENDPOINT + "/" + id;
    return new Promise((resolve, reject) => {
        axios.post(ENDPOINT, formData, config)
            .then(response => {
                setTimeout(() => resolve(response.data), WAITING_TIME);
                //resolve(response.data);
            })
            .catch(error => {
                if (error.response) {
                    // The request was made, but the server responded with a status code other than 2xx
                    if (error.response.status === 404) {
                        reject(error.response.data.message);
                    }
                    reject({ "message": JSON.stringify(error.response.data.errors) });
                } else if (error.request) {
                    // The request was made but no response was received
                    reject("No response received");
                } else {
                    // Something else went wrong
                    reject("Request failed: " + error.message);
                }
            });
    });
}
export function deleteCategoryApi(formData) {
    // getting data
    const access_token = formData.get("access_token");
    const id = formData.get("id");
    // preparing config
    const config = {
        headers: {
            'Authorization': `Bearer ${access_token}`
        }
    };
    const ENDPOINT = CATEGORY_API_ENDPOINT + "/" + id;
    return new Promise((resolve, reject) => {
        axios.delete(ENDPOINT, config)
            .then(response => {
                setTimeout(() => resolve(response.data), WAITING_TIME);
            })
            .catch(error => {
                if (error.response) {
                    // getting sample message
                    if (error.response.status === 404) {
                        reject(error.response.data.message);
                    }
                    reject({ "message": JSON.stringify(error.response.data.errors) });
                } else if (error.request) {
                    // The request was made but no response was received
                    reject("No response received");
                } else {
                    // Something else went wrong
                    reject("Request failed: " + error.message);
                }
            });
    });
}