import React, { useState } from 'react';
import Box from '@mui/material/Box';
import Typography from '@mui/material/Typography';

const DashboardCard = ({ title, count, onClick }) => {
  const cardGradient = 'linear-gradient(45deg, #2196F3 30%, #21CBF3 90%)';

  const [elevated, setElevated] = useState(false);

  const cardStyles = {
    marginTop: '24px',
    background: cardGradient,
    color: 'white',
    padding: '16px',
    borderRadius: '8px',
    boxShadow: elevated
      ? '0px 8px 12px 0px rgba(33, 203, 243, 0.6)'
      : '0px 3px 5px 2px rgba(33, 203, 243, 0.3)',
    width: '100%',
    cursor: 'pointer',
    transition: 'box-shadow 0.3s', // Add a transition for the box-shadow property
  };

  return (
    <Box
      style={cardStyles}
      onClick={onClick}
      onMouseEnter={() => setElevated(true)}
      onMouseLeave={() => setElevated(false)}
    >
      <Typography variant="h6">{title}</Typography>
      <Typography variant="h3" style={{ marginTop: '10px' }}>
        {count}
      </Typography>
    </Box>
  );
};

export default DashboardCard;
