import Button from "@mui/material/Button";
import TextField from "@mui/material/TextField";
import FormControlLabel from "@mui/material/FormControlLabel";
import Checkbox from "@mui/material/Checkbox";
import Link from "@mui/material/Link";
import Grid from "@mui/material/Grid";
import Box from "@mui/material/Box";
import Typography from "@mui/material/Typography";
import Container from "@mui/material/Container";
import CircularProgress from '@mui/material/CircularProgress';
import { blue, red } from '@mui/material/colors';
import { DropzoneArea } from 'material-ui-dropzone';
// core import
import { useSelector, useDispatch } from 'react-redux';
import { useNavigate } from 'react-router-dom';
import React, { useEffect, useState } from 'react';

// Dialogs

import Dialog from '@mui/material/Dialog';
import DialogActions from '@mui/material/DialogActions';
import DialogContent from '@mui/material/DialogContent';
import DialogContentText from '@mui/material/DialogContentText';
import DialogTitle from '@mui/material/DialogTitle';

import {
  // selectors
  loginStatus,
  loginError,
  selectUserRegistrationValidationError,
  selectAccessToken,

  // actions

  // thunks
  // } from './../../../features/user/userSlice';
} from 'features/user/userSlice';



import {
  // selectors
  // main data (၂) မျိုး
  selectMenuList,
  selectSelectedMenu,
  // CRUD Status (၄) မျိုး
  selectMenuStatus,
  selectMenuStoreStatus,
  selectMenuUpdateStatus,
  selectMenuDeleteStatus,
  // Error (၂) မျိုး
  selectMenuError,
  selectMenuErrors,

  // actions
  // local state ပြောင်းတာတွေ
  setCategories,
  setSelectedMenu,
  updateSelectedMenu,
  // local status ပြောင်းတာတွေ
  setMenuStatus,
  setMenuStoreStatus,
  setMenuUpdateStatus,
  setMenuDeleteStatus,
  // Validation Errors တွေ set လုပ်ဖို့
  setMenuErrors

} from 'features/main/mainSlice';


import {

  // thunks
  // CRUD Thunks  တွေ။
  selectMenuListAsyncThunk,
  storeMenuAsyncThunk,
  updateMenuAsyncThunk,
  deleteMenuAsyncThunk,
} from 'features/menu/menuSlice';



const MenuEditFormPage = () => {

  // core
  const navigate = useNavigate();
  const dispatch = useDispatch();

  // internal state
  const [formData, setFormData] = useState({
    placement: 0,
    shop_id: 0,
    name: '',
    description: '',
  });


  const [files, setFiles] = useState({
    background_image: null,
    shop_image: null
  });



  // selector hooks
  // menu
  // လိုတာတွေပဲ Select လုပ်မလား?
  // အကုန် ယူမလား?
  // module တစ်ခုမှာ လိုတာတွေပဲယူလိုက်တာ ကောင်းပါတယ်။
  const menuUpdateStatus = useSelector(selectMenuUpdateStatus);
  const error = useSelector(selectMenuError);
  const errors = useSelector(selectMenuErrors);
  // ဒါက supllimentry လိုတဲ့ data တွေ။
  // shop
  const selectedMenu = useSelector(selectSelectedMenu);
  // user
  // ဒီမှာလည်း 
  const accessToken = useSelector(selectAccessToken);

  const handleChange = (e) => {
    const { name, value } = e.target;
    console.log({ name, value });
    dispatch(updateSelectedMenu({ name, value }));
    setFormData({ ...formData, [name]: value });
  };

  const handleSubmit = (event) => {
    console.log(event);
    event.preventDefault();

    // validate form data
    // Validation logic
    const validationErrors = {};

    /*
    if (!formData.name) {
      validationErrors.name = 'Name is required';
    }

    if (!formData.description) {
      validationErrors.description = 'Description is required';
    }
    */
    console.log(validationErrors);

    if (Object.keys(validationErrors).length > 0) {
      // alert("validation error");
      // setErrors(validationErrors);
      dispatch(setMenuErrors(validationErrors));
    } else {
      dispatch(setMenuErrors({}));
      const data = new FormData(event.target);
      // add files 
      if (files["background_image"] !== undefined) {
        data.append('background_image', files["background_image"]);
      }
      dispatch(updateMenuAsyncThunk(data));
    }
  };




  const handleClose = () => {
    dispatch(setMenuUpdateStatus('idle'));
  };


  const handleFileChanges = (event) => {
    const { name, current_files } = event;
    console.log('Name & Files:', name, current_files);
    setFiles({ ...files, [name]: current_files.length == 0 ? null : current_files[0] });

  }

  useEffect(() => {
    alert("menuUpdateStatus is " + menuUpdateStatus);
    if (menuUpdateStatus == "succeeded") {
      dispatch(setMenuUpdateStatus("idle"));
      navigate(-1); // This is equivalent to pressing the back button
    }
  }, [menuUpdateStatus]);


  return (
    <Container component="main" maxWidth="xs">
      <Box
        sx={{
          marginTop: 8,
          display: "flex",
          flexDirection: "column",
          alignItems: "center",
        }}
      >
        <Typography component="h1" variant="h5">
          Edit Menu
        </Typography>
        <Box component="form" onSubmit={handleSubmit} noValidate sx={{ mt: 1 }}>
          <input type="hidden" name="id" value={selectedMenu.id} placeholder="Hidden Input for Selected Menu Id" />
          <input type="hidden" name="access_token" value={accessToken} placeholder="Hidden Input" />
          <input type="hidden" name="category_id" value={selectedMenu.category_id} placeholder="Hidden Input for Selected Shop Id" />
          <input type="hidden" name="placement" value={0} placeholder="Menu Placement" />
          <input type="hidden" name="is_active" value={1} placeholder="Active Flag" />
          <input type="hidden" name="visit_count" value={0} placeholder="Visit Count" />
          { /* name */}
          <TextField
            id="name"
            label="Name"
            name="name"
            value={selectedMenu.name ?? ""}
            onChange={handleChange}
            error={errors.hasOwnProperty("name")}
            helperText={errors.name}

            autoComplete="name"
            margin="normal"
            required
            fullWidth
            autoFocus
          />

          { /* description */}
          <TextField
            id="description"
            label="Description"
            name="description"
            value={selectedMenu.description ?? ""}
            autoComplete="description"
            onChange={handleChange}
            error={errors.hasOwnProperty("description")}
            helperText={errors.description}

            margin="normal"
            required
            fullWidth
          />


          <Typography>Background Image {files["background_image"]?.name}</Typography>
          <DropzoneArea
            acceptedFiles={['image/*']}
            onChange={(selectedFiles) => handleFileChanges({ "current_files": selectedFiles, "name": "background_image" })}
          />


          <Box sx={{ m: 1, position: 'relative' }}>
            <Button
              type="submit"
              fullWidth
              variant="contained"
              sx={{ mt: 3, mb: 2 }}
              disabled={menuUpdateStatus == 'loading'}
            >
              Update Menu
            </Button>
            {menuUpdateStatus == 'loading' && (
              <CircularProgress
                size={24}
                sx={{
                  color: blue[500],
                  position: 'absolute',
                  top: '50%',
                  left: '50%',
                  marginTop: '-12px',
                  marginLeft: '-12px',
                }}
              />
            )}
          </Box>


          <Grid container>
            <Grid item xs>

            </Grid>
            <Grid item>
              <Link href="/shops" variant="body2">
                {"Back to shop list"}
              </Link>
            </Grid>
          </Grid>
        </Box>
      </Box>

      <Dialog
        open={menuUpdateStatus == "error"}
        onClose={handleClose}
        aria-labelledby="alert-dialog-title"
        aria-describedby="alert-dialog-description"
      >
        <DialogTitle id="alert-dialog-title">
          {"Error"}
        </DialogTitle>
        <DialogContent>
          <DialogContentText id="alert-dialog-description">
            {error}
          </DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button onClick={handleClose} autoFocus>
            OK
          </Button>
        </DialogActions>
      </Dialog>
    </Container>
  );
};
export default MenuEditFormPage;