import React from 'react';
import { useState } from 'react';
import { useEffect } from 'react';
import { useParams, Link } from 'react-router-dom';
import Box from '@mui/material/Box';
import Typography from '@mui/material/Typography';
import Grid from '@mui/material/Grid';
import Paper from '@mui/material/Paper';
import Button from '@mui/material/Button'; // Import Button component from Material-UI
import Container from '@mui/material/Container'; // Import Button component from Material-UI

import CircularProgress from '@mui/material/CircularProgress';
import { blue, red } from '@mui/material/colors';


import Dialog from '@mui/material/Dialog';
import DialogActions from '@mui/material/DialogActions';
import DialogContent from '@mui/material/DialogContent';
import DialogContentText from '@mui/material/DialogContentText';
import DialogTitle from '@mui/material/DialogTitle';


// ၁. state management က အဖွဲ့တွေ
import { useSelector, useDispatch } from 'react-redux';
// react router 
import { useNavigate } from 'react-router-dom';


// ၂. မဖြစ်မနေ သုံးကြမယ့်  Slice က function တွေ
import {
  // thunks
  // selectors
  selectSelectedShop,
  selectShopDeleteStatus,
  selectShopError,
  // actions
  setShopDeleteStatus,
  setSelectedCategory
} from 'features/main/mainSlice';
import {
  // thunks
  deleteShopAsyncThunk
} from 'features/shop/shopSlice';

import {
  // thunks
  // selectors
  selectAccessToken
  // actions
} from 'features/user/userSlice';

// import { setSelectedCategory } from 'features/category/categorySlice';






const ShopDetailPage = () => {


  // core
  const { id } = useParams();
  const dispatch = useDispatch();
  const navigate = useNavigate();


  // internal state
  const [open, setOpen] = useState(false);


  // selector hooks
  const access_token = useSelector(selectAccessToken);

  // shop section
  const shop = useSelector(selectSelectedShop);
  const shop_delete_status = useSelector(selectShopDeleteStatus);
  const error = useSelector(selectShopError);
  // const shop = shopData.find((shop) => shop.id === parseInt(id, 10));

  const handleClose = (id, access_token) => {
    console.log("id is " + id);
    console.log("access_token is " + access_token);
    if (id !== undefined) {
      // dispatch delete shop api 
      let formData = new FormData();
      formData.append("access_token", access_token);
      formData.append("id", id);
      dispatch(deleteShopAsyncThunk(formData));
    }
    setOpen(false);
  };

  const handleErrorDialogClose = () => {
    dispatch(setShopDeleteStatus('idle'));
  }
  
  
  const handleCategoryCardClick = (category) => {
    // set category detail and
    dispatch(setSelectedCategory(category))
    // go to category detail page
    navigate(`/categories/${category.id}`)
  }
  useEffect(() => {
    if(shop_delete_status == "succeeded"){
      // set back to idle 
      dispatch(setShopDeleteStatus("idle"));
      // and then go back to shops
      navigate("/shops");
    }
  }, [shop_delete_status]); // Include history as a dependency
  




  // Main UI Component
  if (!shop) {
    return (
      <Box>
        <Typography variant="h4">Shop not found</Typography>
      </Box>
    );
  }

  return (

    <Container  >

      <Box>
        <Typography variant="h4">{shop.name}</Typography>
        <Typography variant="body2">{shop.description}</Typography>

        <Typography variant="h5" style={{ marginTop: '20px' }}>
          Available Categories
        </Typography>
        <Button
          component={Link}
          to={"edit"}
          variant="contained"
          color="primary"
          style={{ marginTop: '20px', marginBottom: '20px' }}
        >
          Edit Shop Info
        </Button>

        <br></br>

        <Box sx={{ m: 1, position: 'relative' }}>

          <Button
            onClick={() => setOpen(true)}
            variant="contained"
            style={{ marginTop: '20px', marginBottom: '20px' }}
          >
            Delete Shop Info 
          </Button>
          Shop Delete Status : { shop_delete_status }
        </Box>

        <br></br>

        <Button
          component={Link}
          to="/categories/new"
          variant="contained"
          color="primary"
          style={{ marginTop: '20px', marginBottom: '20px' }}
        >
          Create New Category
        </Button>
        <Grid container spacing={3}>
          {shop.categories.map((category) => (
            <Grid item xs={12} sm={6} md={4} key={category.id}>
                <Container onClick={() => handleCategoryCardClick(category) } >
                <Paper elevation={3} style={{ padding: '16px', cursor: 'pointer' }}>
                  <Typography variant="h6">{category.name}</Typography>
                  <Typography variant="body2">{category.description}</Typography>
                </Paper>
                </Container> 
            </Grid>
          ))}
        </Grid>


      </Box>



      {/* delete confirmation dialog */}
      <Dialog
        open={open}
        // onClose={() => handleClose(id)}
        aria-labelledby="alert-dialog-title"
        aria-describedby="alert-dialog-description"
      >
        <DialogTitle id="alert-dialog-title">
          Delete Confirmation!
        </DialogTitle>
        <DialogContent>
          <DialogContentText id="alert-dialog-description">
            Are you sure to delete this shop info?
          </DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button onClick={() => handleClose(0)} autoFocus>
            Cancel
          </Button>
          <Button onClick={() => handleClose(id, access_token)} autoFocus>
            OK
          </Button>
        </DialogActions>
      </Dialog>


      {/* delete error dialog */}
      <Dialog
        open={shop_delete_status == 'error'}
        aria-labelledby="alert-dialog-title"
        aria-describedby="alert-dialog-description"
      >
        <DialogTitle id="alert-dialog-title">
          Error!
        </DialogTitle>
        <DialogContent>
          <DialogContentText id="alert-dialog-description">
            { error }
          </DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button onClick={handleErrorDialogClose} autoFocus>
            OK
          </Button>
        </DialogActions>
      </Dialog>
    </Container>
  );
};

export default ShopDetailPage;
