import * as React from 'react';
import Stack from '@mui/material/Stack';
import Button from '@mui/material/Button';
import { Link } from 'react-router-dom';
import { useSelector, useDispatch } from 'react-redux';
import { useNavigate } from 'react-router-dom';

import {
    logout
} from './../../../features/user/userSlice';

export default function BasicButtons({ path }) {

    // core
    const navigate = useNavigate();
    const dispatch = useDispatch();

    const handleLogoutClick = () => {
        // dispatch logout action to user
        // clear existing data in shop 
        dispatch(logout());
        navigate("/login");
    }
    return (
        <Stack spacing={2} alignItems="center" justifyContent="center" margin={2} direction="row" border="1px solid gray" borderRadius={1}>
            <Button variant={path == "dashboard" ? "contained" : "text"} component={Link} to="/dashboard" >Dashboard</Button>
            <Button variant={path == "shops" ? "contained" : "text"} component={Link} to="/shops" >Shops</Button>
            <Button variant={"text"} onClick={handleLogoutClick} >Logout</Button>
        </Stack>
    );
}